* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
}

body {
  background-color: rgb(43, 43, 43);
}

.container {
  width: 100%;
  display: flex;
  flex-direction: column;
  /* flex-wrap: wrap; */
  justify-content: flex-start;
  align-items: center;
  height: 100%;
  background-color: rgb(43, 43, 43);
}

.header {
  margin-top: 4%;
  text-align: center;
  margin-bottom: 30px;
}

.header h1 {
  color: rgb(255, 255, 255);
  font-size: 1.8em;
} 

.header h3 {
  color: rgb(194, 166, 6);
  font-size: 1.1em;
} 

.foto {
  width: 150px;
  border-radius: 100%;
  box-shadow: 0 0 1em gold;
  margin-bottom: 10px;
}

.dev {
  margin-top: 5px;
  color: white !important;
  font-size: 1.3em !important;
}

.content {
  display: flex;
  /* max-width: 600px; */
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 10px;
}

.square1 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  width: 280px;
  height: 150px;
  background-color: rgb(61, 60, 60);
  transform: scale(0.9);
  transition: all 0.5s ease;
}

.square2 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  width: 280px;
  height: 150px;
  background-color: rgb(61, 60, 60);
  transform: scale(0.9);
  transition: all 0.5s ease;

}

.square3 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  width: 280px;
  height: 150px;
  background-color: rgb(61, 60, 60);
  transform: scale(0.9);
  transition: all 0.5s ease;
}

.square4 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  width: 280px;
  height: 150px;
  background-color: rgb(61, 60, 60);
  transform: scale(0.9);
  transition: all 0.5s ease ;
}

.icon {
  font-size: 40px;
  margin-bottom: 5px;
}

.square1:hover,
.square2:hover,
.square3:hover,
.square4:hover {
  transform: scale(1);
  box-shadow: 0 0 0.2em gold;
  color: gold;
  font-weight: 900;
  font-size: 1.5em;
  cursor: pointer;
}

.port-container {
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.port {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 40px;
  }

  .card {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 350px;
  }

.port a {
  width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transform: scale(0.9);
  transition: all 0.5s ease ;
  text-decoration: none;
  color: white;
  }

.saiba-mais {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5em;
  background-color: rgba(255, 255, 255, 0.0);
  color: rgba(255, 255, 255, 0);
  text-align: center;
  width: 100%;
  height: 30px;
  margin-top: -30px;
  z-index: 10;
  transition: all 0.8s ease;
}

.port img {  
  width: 100%;
 
  }

.port a:hover {  
  transform: scale(1);
  box-shadow: 0 0 0.4em gold;
  color: gold;
  font-weight: 900;
  cursor: pointer;
  }

  .port a:hover  img{  
    box-shadow: 0 0 0.4em gold;
    }

  .port a:hover .saiba-mais{
    display: block;
    margin-top: -20%;
    background-color: rgba(255, 255, 255, 1);
    color: rgba(0, 0, 0, 1);

  }

.port-btn {
  position: fixed;
  top: 20px;
  right: 20px;
  display: flex;
  width: 200px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  padding: 10px;
  font-size: 1.2em;
  background-color: rgb(61, 60, 60);
  transform: scale(0.9);
  transition: all 0.5s ease ;
  margin-bottom: 15px;
  z-index: 999;
}
.port-btn:hover {
  
  transform: scale(1);
  box-shadow: 0 0 0.2em gold;
  color: gold;
  font-weight: 900;
  cursor: pointer;
}

@media only screen and (max-width: 890px) {
  .container {
    height: 150vh;
  }

  .port {
    margin-bottom: 10px;
  }
}

@media only screen and (max-width: 600px) {
  .header {
    font-size: 1.1em;
  }

  .foto {
    width: 30%
  }
  
  .content {
    width: 90%;
    align-items: center;
    justify-content: center;
  }
  .square1,
  .square2,
  .square3,
  .square4 {
   font-size: 1.3em;
  }
  .icon {
    font-size: 1.8em;
  }

  .card {
    width: 100%;
    height: 200px;
  }
  .port a {
    transform: scale(1);
  }
}